
.viewfinder{
background: linear-gradient($colorDegradadoAzul, $colorDegradadoAzulFin);
display: flex;
justify-content: center;
align-items: center;
padding-top:60px;
padding-bottom: 30px;

&__card{
  position: relative;
  background-color: $backgroundHeader;
  width: 240px;
}
&__button{
  position: absolute;
  top:-40px;
  left: 0;
  width: 75px;
  height: 30px;
  &-reset{
    background-color: $colorBackgroundReset;
  border-radius: 15px;
  border: 1px solid $colorTextTagsCard;
  text-transform: uppercase;
  font-family: 'OpenSans',serif;
  font-size: 12px;
  color: $colorTextButtonReset;
  padding:5px 10px;
}
&-icono{
  margin-right: 5px;
  width: 11px;
  height: 20px;
}
}
}
.viewfinder__card-title{
display: flex;

&-titlebox{
  display: flex;
  flex-direction: column;

}
  &-rectangule{
    height: 42px;
    width: 5px;
    background-color: $colorBackgroundRectangule ;
    margin:20px 15px 0 0;
  }
  &-name{
    padding: 20px 14px 5px 0px;
    font-family:'Comic Sans MS', 'Comic Sans', 'TSCu_Comic', san-serif;/*ojo mirar la fuente bien*/
    font-size: 18px;
    line-height:  20px;
    color:$colorTextTitleName;
  }
  &-job{
    padding: 0 14px 5px 0px;
    font-family: 'Comic Sans MS', 'Comic Sans', 'TSCu_Comic', san-serif;/*ojo mirar la fuente bien*/
    font-size: 18px;
    line-height:20px;
    color:$colorTextTitleJob;
  }

}

.viewfinder__icons{
  display: flex;
  justify-content:flex-start;
  margin:10px 0 10px 10px;
  &-box{
    margin-left: 10px;
    border-radius: 22px;
    border: 2px solid  $colorBorderIcons;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $colorBackgroundIcons;
  }
}
.viewfinder__tags{
  display: flex;
  justify-content:flex-start;
  align-items: center;
  margin:5px 20px;
  &-text{
    height: 18px;
    font-size: 13px;
    border-radius: 4px;
    background-color: $colorBackgroundTags;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color:$colorTextTagsCard;
    text-transform: uppercase;
    padding: 0 5px;
  }
}

  @media all and (min-width: 768px){
    .main__2{
      display: grid;
      grid-template-columns: 1fr 369px;
    }
    .main__card{
      padding: 20px 40px 21px 0px;
    }
    .main__form{
       grid-column-start:1;
       grid-row: 1;
    }
    .main__card{
       grid-column:2;
    }
    .viewfinder{
      height: 100%;
      border-radius: 6px;
      box-sizing: border-box;
    
  }
}
